.main{
    display: flex;
    flex-wrap: wrap;
   
}

html{
    scroll-behavior: smooth;
    
}
.grid{
    display: flex;
    flex-wrap: wrap;
    margin-left: 20%;
}
.card {
     box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
     max-width: 300px;
     font-family: arial;
     display: flex;
     font-size: small;
     border-radius: 15px;
     margin: 10px;
     }

.card :hover{
      box-shadow: 100px;
  }   
.imgg{
       
       height:200px;
       width: 300px;
       border-top-left-radius: 15px;
       border-top-right-radius: 15px;
     
  }

.heads{
   padding-left: 15px;
   padding-right: 15px;
   font-family:Raleway,sans-serif;
   color: black;
   
   
}
a{
    color: blue;
    text-decoration: none;
}
#foot{color: white;}
p{
    color: rgb(201, 156, 156);
    font-size: small;
    letter-spacing: 1px;
}
h2{
    color: rgb(44, 24, 64);
}
.nav_bar{
    display: flex;
    justify-content: space-around;
    padding: 20px;
    height:40px;
    width: 100vw;
    position: fixed;
    background-color: white;
    margin-top: 0%;
    top: 0;
    left: 0;
    box-shadow: 0 2px 2px 0 gray;

     
}
.nav_button{
    border-radius: 20px;
    cursor: pointer;
    background-color: blue;
    padding:10px;
    border: 1px solid blue;
    color: white;
    
}
.logoName{
    color: blue ;
    font-size:x-large;
}

.logo_base{
    color: black;
    
}
.heading{
    padding-top:100px;
    padding-left: 300px;

}
.heading_2{
    margin-top: 5%;
    padding-left: 300px;

}
.heading_3{
    margin-top: 20px;
    padding-left: 300px;
}
.heading_4{
    margin-top: 20px;
    padding-left: 300px;
    font-size: 18px;
}
.heading_5{
    margin-top: 20px;
    padding-left: 300px;
}
.top_images{
    display: flex;
    width: 100px;
    height:100%;
    padding-left: 300px;
    
   
}
.image_2{
    padding:2px;
    height: 200px;
    margin: 0%;
    
}
.button_2{
    border-radius: 18px ;
    color: blue;
    padding: 10px;
    border: 1px solid blue;
    cursor: pointer;
    margin: 2px;
}


.button_2:hover {
    background: linear-gradient(269deg,#0048ff,#0096ff);
}
